'use strict'

const nodemailer = require('nodemailer')
const fs = require('fs')
const path = require('path')
const ejs = require('ejs')

let transporter = nodemailer.createTransport({
  // host: 'smtp.ethereal.email',
  service: 'qq', //  https://nodemailer.com/smtp/well-known/
  port: 465, // SMTP 
  secureConnection: true, //  SSL
  auth: {
    user: '171426589@qq.com',
    // qqsmtp
    pass: 'xmzrcehbkyvbcabi'
  }
})

const sendMailFn = (sendMsg) => {
  const template = ejs.compile(fs.readFileSync(path.resolve(__dirname, 'email.ejs'), 'utf8'))

  const html = template({
    errorMsg: sendMsg ? sendMsg.errorMsg : ''
  })

  let mailOptions = {
    from: '"FilmGo " <171426589@qq.com>', // sender address
    to: 'suiyang_sun@163.com', // list of receivers
    subject: 'FilmGo , 5min', // Subject line
    html: html
  }
  // send mail with defined transport object
  transporter.sendMail(mailOptions, (error, info) => {
    if (error) {
      return console.log(error)
    }
    console.log('Message sent: %s', info.messageId)
    // Message sent: <04ec7731-cc68-1ef6-303c-61b0f796b78f@qq.com>
  })
}

module.exports = sendMailFn



